@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

input[type="submit"], button { cursor: pointer; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-sepa:before { content: ""; }

.icon-skrill:before { content: ""; }

@font-face { font-family: futura; src: url("fonts/futura.eot"); src: url("fonts/futura.eot?#iefix") format("embedded-opentype"), url("fonts/futura.woff2") format("woff2"), url("fonts/futura.woff") format("woff"), url("fonts/futura.ttf") format("truetype"); font-weight: normal; font-style: normal; }
@font-face { font-family: futura-light; src: url("fonts/futura-light.eot"); src: url("fonts/futura-light.eot?#iefix") format("embedded-opentype"), url("fonts/futura-light.woff2") format("woff2"), url("fonts/futura-light.woff") format("woff"), url("fonts/futura-light.ttf") format("truetype"); font-weight: normal; font-style: normal; }
body { font-size: 16px; line-height: 1.4; font-family: "futura-light", Arial, sans-serif; }

.out { position: relative; min-width: 320px; min-height: 450px; height: 100%; }
@media only screen and (max-width: 1000px) { .out { height: auto; } }

.main { height: 100%; background-image: url(../img/main.jpg); background-size: cover; background-position: top center; }
@media only screen and (max-width: 1020px) { .main { height: 800px; padding: 0 10px; background-position: 70% 50%; text-align: center; font-size: 0; }
  .main:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; } }

.motto { max-width: 1050px; padding-top: 33px; font-size: 48px; text-transform: uppercase; }
.motto__in { padding: 18px 60px; background: rgba(0, 0, 0, 0.8); color: #FFF; }
.motto span { color: #ffb100; }
@media only screen and (max-width: 1460px) { .motto { max-width: 700px; font-size: 45px; } }
@media only screen and (max-width: 1020px), (max-height: 500px) { .motto { display: inline-block; font-size: 35px; }
  .motto__in { padding: 18px 30px; } }
@media only screen and (max-width: 650px) { .motto { font-size: 30px; }
  .motto__in { padding: 18px 10px; } }

.footer { position: absolute; bottom: 0; left: 0; right: 0; padding: 42px 0; background: #2A2A33; }
.footer__in { display: table; width: 100%; table-layout: fixed; }
.footer__bl { display: table-cell; text-align: center; vertical-align: top; }
@media only screen and (max-height: 1000px) { .footer { padding: 30px 0; } }
@media only screen and (max-width: 1020px), (max-height: 500px) { .footer { position: relative; }
  .footer__in, .footer__bl { display: block; }
  .footer__bl { margin-bottom: 30px; } }

.logo { display: block; margin-top: 30px; }
.logo img { max-width: 100%; }

.counter { margin: 0 auto; }
.counter__ttl { font-size: 36px; text-transform: uppercase; letter-spacing: 0.05em; color: #FFF; }

.form { position: relative; top: -135px; width: 100%; max-width: 300px; margin: 0 auto -135px; padding: 32px 38px 7px; border: 2px solid #2a2a33; border-radius: 24px; background-color: #6d6d76; background-image: linear-gradient(#6d6d76, #2a2a33); }
.form__field { margin-bottom: 10px; }
.form__ttl { margin-bottom: 20px; font-size: 24px; color: #b7b7bb; line-height: 1; }
@media only screen and (max-width: 1020px), (max-height: 500px) { .form { top: 0; margin-bottom: 0; } }

.btn { display: inline-block; margin-top: 15px; padding: 17px 44px; text-transform: uppercase; font-size: 15px; background: #ffc600; color: #393942; border-radius: 40px; line-height: 1; letter-spacing: 0.05em; font-family: "futura-light", Arial, sans-serif; transition: 0.3s; }
.btn:hover { background: #ffb100; }

input { display: block; width: 100%; padding: 11px 15px; font-size: 16px; color: #2a2a33; background: #D1D1D4; font-family: "futura-light", Arial, sans-serif; }
input::placeholder { color: #2a2a33; }

.flip-clock-wrapper { margin: 20px 0 0; background-color: rgba(0, 0, 0, 0.1); box-shadow: 0 1px 2px rgba(255, 255, 255, 0.4), inset 0 1px 2px #000; border-radius: 9px; }
.flip-clock-wrapper ul { width: 52px; height: 64px; margin: 0 2px; font-size: 52px; line-height: 64px; }
.flip-clock-wrapper ul li { line-height: 64px; }
.flip-clock-wrapper ul li a div div.inn { text-shadow: 0 -1px 0 black; font-size: 52px; background-color: #36363e; color: #BBBCC1; }
.flip-clock-wrapper ul li a div.up:after { top: 28px; }

.flip-clock-dot.top, .flip-clock-dot.bottom { display: none; }

.flip-clock-divider { width: 10px; }
.flip-clock-divider:first-child { width: 10px; }
.flip-clock-divider .flip-clock-label { top: 100%; margin-top: 17px; text-transform: capitalize; font-size: 16px; color: #f49e00; font-family: "futura-light", Arial, sans-serif; }


